@font-face {
    font-family: 'rounded-mplus-1p-regular';
    src: url('../rounded-mplus-1p-regular/rounded-mplus-1p-regular.woff2') format('woff2'),
         url('../rounded-mplus-1p-regular/rounded-mplus-1p-regular.woff') format('woff'),
         url('../rounded-mplus-1p-regular/rounded-mplus-1p-regular.ttf') format('truetype');
}

html, body {
  font-family: 'rounded-mplus-1p-regular';
}

#form {
  width: 400px;
  padding: 1em;
  border: 1px solid #CCC;
  border-radius: 1em;

  div + div {
    margin-top: 1em;
  }

  label {
    /* すべてのラベルを同じサイズにして、きちんと揃える */
    display: inline-block;
    width: 90px;
    text-align: right;
  }

  input, textarea {
      /* すべてのテキストフィールドのフォント設定を一致させる
         デフォルトで、textarea は等幅フォントが設定されている */
      font: inhelit;

      /* すべてのテキストフィールドを同じサイズにする */
      width: 300px;
      box-sizing: border-box;
  }

  textarea {
      /* 複数行のテキストフィールドをラベルにきちんと揃える */
      vertical-align: top;

      /* テキスト入力に十分な領域を与える */
      height: 5em;

      /* ユーザが textarea を垂直方向にリサイズできるようにする
         これが動作しないブラウザもある */
      resize: vertical;
  }

  .button {
      /* ボタンを他のテキストフィールドと同じ場所に置く */
      padding-left: 90px; /* label 要素と同じサイズ */
  }
  button {
      /* このマージンは、ラベルとテキストフィールドの間のスペースと
         おおよそ同じスペースを表す */
      margin-left: .5em;

      width: 180px;
  }
}
